.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #09d3ac;
}

body {
    background-image: url('./static/images/lemonjing_background3.jpg');
    background-repeat: repeat;
}

.adsbygoogle {
    border: 3px dotted rgba(192, 192, 192, 0.5);
    background-image: url('./static/images/ads_background.png');
    background-repeat: repeat;
}
